import {take, put, call, fork, select} from 'redux-saga/effects'
import * as API from "../api"
import * as actions from '../actions';
//
export function* searchRoleList() {
    while (true){
        yield take(actions.ROLE_TABLE_LIST_FETCH)
        let posts = yield call(API.fetchRoleListApi);
        yield put(actions.roleTalbeList(posts.data))
    }
}
//
export function* deleteRole() {
    while (true) {
        const action = yield take(actions.ROLE_DELETE)
        yield call(API.fetchRoleDeleteApi, {id: action.result})
        yield put(actions.roleTalbeListFetch(true))
        action.callback()
    }
}

//
export function* viewRole() {
    while (true) {
        const action = yield take(actions.ROLE_DETAIL_VIEW)
        yield put(actions.viewRoleDetail(null, [], [], true))
        let [roles,users]= yield [call(API.fetchRoleDetailApi, {list: [{roleId: action.id}]})
            , call(API.fetchRoleUserDetailApi, {id: action.id})]
        yield put(actions.viewRoleDetail(null, roles.data, users.data, false))
        $("#viewRoleTree").mCustomScrollbar({
            scrollInertia: 250,
            theme: 'dark'
        });
        $("#userListScroll").mCustomScrollbar({
            scrollInertia: 250,
            theme: 'dark'
        });
    }
}

// 
export function* fetchMenuList() {
    while (true) {
        const action = yield take(actions.MENU_TREE_LIST_ADD)
        yield put(actions.roleLoadingChange(true))
        let posts = yield call(API.fetchMenuList);
        yield put(actions.menuTreeList(posts))
        action.callback()
    }
}

// 
export function* editRole() {
    while (true) {
        const action = yield take(actions.ROLE_EDIT_TREE_ID)
        yield put(actions.roleLoadingChange(true))
        let [checkedroles,allRoles] = yield [call(API.fetchRoleDetailApi, {list: [{roleId: action.id}]}),
            call(API.fetchMenuList)]
        yield put(actions.editRoleTree({
            checked: checkedroles.data.length > 0 ? checkedroles.data[0].menuBeans : [],
            all: allRoles.menuBeans,
            rootTree: checkedroles.data[0]
        }))
        action.callback()
    }
}
//  
export function* userAddRole() {
    while (true) {
        //Id
        let postAction = yield take(actions.ROLE_INSERT_AND_UPDATE);
        yield put(actions.menuSaveLoading(true))
        let posts = yield call(API.fetchRoleUpdateApi, postAction.result);
        let id = posts.data.id;
        let treeData = yield select(function (state) {
            return state.role.MenuTree
        })
        let rootTree = yield select(function (state) {
            return state.role.rootTree
        })
        //
        let tree = [{
            menuId: rootTree.id,
            roleId: id,
            name: rootTree.menuName,
            permission: ""
        }];
        let dataTree = []
        treeData.arr.forEach((item)=> {
            item.forEach((it)=> {
                if (it.checked) {
                    if(it.trueName){
                        //
                        dataTree.push({
                            "enable": 1,
                            "menuId": it.truePid,
                            "paramId": it.id,
                            "paramKey": it.paramKey,
                            "paramValue": it.trueName,
                            "roleId": id
                        })
                    }else if (it.truePid) {
                        tree.push({
                            "enable": 1,
                            "menuId": it.truePid,
                            "permission": it.code,
                            "roleId": id
                        })
                    } else {
                        if (it.flag !== 'fakeParent') {
                            tree.push({
                                "enable": 1,
                                "menuId": it.id,
                                "permission": "",
                                "roleId": id
                            })
                        }
                    }
                }else {
                    if(it.trueName){
                        //
                        dataTree.push({
                            "enable": 1,
                            "menuId": it.truePid,
                            "roleId": id
                        })
                    }
                }

            })
        })
        //
        yield call(API.saveRolePermissionApi, {list: tree})
        //
        yield call(API.saveRoleDataPermissionApi, {list: dataTree})
        yield put(actions.menuSaveLoading(false))
        //   
        yield put(actions.roleTalbeListFetch(false))
        postAction.callback()
    }
}


export default function*() {
    yield fork(fetchMenuList);
    yield fork(searchRoleList);
    yield fork(viewRole);
    yield fork(deleteRole);
    yield fork(editRole);
    yield fork(userAddRole);
}


