//
export function fetchRoleListApi(params) {
    return axios.post('/role/read/list', params,"authority_role")
        .then(function (result) {
            return result.data;
        })
}
//
export function fetchRoleDetailApi(params) {
    return axios.post('/role/read/permissionTree', params)
        .then(function (result) {
            return result.data;
        })
}

//
export function fetchRoleUserDetailApi(params) {
    return axios.post('/role/read/userList', params)
        .then(function (result) {
            return result.data;
        })
}
//
export function fetchRoleDeleteApi(params) {
    return axios.post('/role/delete', params)
        .then(function (result) {
            return result.data;
        })
}
//   
export function fetchRoleUpdateApi(params) {
    return axios.post('/role/updateAndInsert', params)
        .then(function (result) {
            return result.data;
        })
}
//-  
export function saveRolePermissionApi(params) {
    return axios.post('/role/update/permission', params)
        .then(function (result) {
            return result.data;
        })
}
//- 
export function saveRoleDataPermissionApi(params) {
    return axios.post('/param/updateAndInsert', params)
        .then(function (result) {
            return result.data;
        })
}

//
export function fetchMenuList(params) {
    return axios.post('/menu/read/tree', params)
        .then(function (result) {
            return result.data.data[0];
        })
}


