import React, {Component} from 'react'
import {bindActionCreators} from 'redux'
import {connect} from 'react-redux'
import {withStyles} from 'material-ui/styles';
import * as actions from '../actions'
import Tabs, {Tab} from 'material-ui/Tabs';
import Button from 'material-ui/Button';
import Modal from 'material-ui/Modal';
import Spin from '@/components/spin'
import Message from '@/components/Message'
import {
    PagingState, DataTypeProvider,
    IntegratedPaging
} from '@devexpress/dx-react-grid';
import {
    Grid,
    Table, TableHeaderRow,
    PagingPanel
} from '@devexpress/dx-react-grid-material-ui';
import {Form, Input, Tree} from '@/components'
import "./style.less"
import classzz from '@/classes'
import 'malihu-custom-scrollbar-plugin/jquery.mCustomScrollbar.css'
import 'jquery-mousewheel'
import 'malihu-custom-scrollbar-plugin'
import _ from 'lodash'

const styles = theme => ({
    primary: classzz.Button.primary,
    root: {
        margin: '0 10px'
    },
    paper: {
        top: '50%',
        left: '50%',
        transform: `translate(-${50}%, -${50}%)`,
        position: 'absolute',
        width: 800,
        boxShadow: theme.shadows[5],
        padding: 0,
    }
});
let scrollbar = function () {
    $("#treebox").mCustomScrollbar({
        scrollInertia: 250,
        theme: 'dark'
    });
}

@withStyles(styles)
class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
            value: 0,
            page: 1,
            minHeight: 0,
            open: false,
            editRole: null,
            searchValue: ""
        }
    }

    handleChange(event, value) {
        var treeObj = $.fn.zTree.getZTreeObj("systemRoleTree");
        var changeChecked = treeObj.getChangeCheckedNodes();
        this.props.actions.menuTreeUpdate(this.state.value, changeChecked)
        this.setState({value}, scrollbar);
    };

    Submit(values) {
        let that = this
        //store checked
        var treeObj = $.fn.zTree.getZTreeObj("systemRoleTree");
        var changeChecked = treeObj.getChangeCheckedNodes();
        this.props.actions.menuTreeUpdate(this.state.value, changeChecked)
        //
        this.props.actions.roleUpdateInsert(values, () => {
            Message.success("")
            that.setState({page: 1})
        })
    }

    componentDidMount() {
        this.setState({
            minHeight: document.body.offsetHeight - 110
        })
        this.props.actions.roleTalbeListFetch(true)
    }

    handleOpen = () => {
        this.setState({open: true});
    };

    handleClose = () => {
        this.setState({open: false});
    };

    view(id) {
        this.props.actions.viewRoleDetail(id)
        this.setState({open: true});
    };

    NewRole() {
        this.setState({page: 2, editRole: null, value: 0})
        this.props.actions.menuTreeListAdd(() => {
            $("#treebox").mCustomScrollbar({
                scrollInertia: 250,
                theme: 'dark'
            });
        })
    }

    delete(id) {
        this.props.actions.deleteRole(id, function () {
            Message.success('')
        })
    }

    edit(role) {
        this.setState({
            page: 2,
            editRole: role,
            value: 0
        })
        this.props.actions.editRoleTreeId(role.id, () => {
            $("#treebox").mCustomScrollbar({
                scrollInertia: 250,
                theme: 'dark'
            });
        })
    }

    onChangeSearch(e) {
        if (e.target.value === "") {
            var reg = new RegExp(e.target.value);
            var searchArr = _.dropRightWhile(this.props.role.RoleCache, function (o) {
                return !reg.test(o.roleName)
            })
            this.props.actions.roleTalbeListSearch(searchArr)
        }
        this.setState({searchValue: e.target.value})
    }

    onEnter(e) {
        if (e && e.keyCode === 13) {
            var reg = new RegExp(this.state.searchValue);
            var searchArr = _.filter(this.props.role.RoleCache, function (o) {
                return reg.test(o.roleName)
            })
            this.props.actions.roleTalbeListSearch(searchArr)

        }
    }

    render() {
        const {classes, role} = this.props
        const {value, page} = this.state;
        ///
        const ViewUserRole = props => {
            return (
                <DataTypeProvider
                    formatterComponent={({row}) => {
                        return (
                            <a className="table-btn" href="javascript:;" onClick={() => this.view(row.id)}></a>
                        )
                    }}
                    {...props}
                />
            )
        }
//
        const CurrencyTypeProvider = props => (
            <DataTypeProvider
                formatterComponent={({row}) => {
                    return (
                        <span>
                         {AuthorityComponent.default(() => {
                             return (
                                 <a className="table-btn" href="javascript:;" onClick={() => this.edit(row)}></a>
                             )
                         }, 'authority_role_edit')}
                        {AuthorityComponent.default(() => {
                            return (
                                <a className="table-btn" href="javascript:;" style={{marginLeft: 5}}
                                   onClick={() => this.delete(row.id)}></a>
                            )
                        }, 'authority_role_delete')}
                    </span>
                    )
                }}
                {...props}
            />
        );
        const TableComponent = (props) => (
            <Grid
                rows={props.rows}
                columns={[
                    {name: '__id__', title: ''},
                    {name: 'roleName', title: ''},
                    {name: 'remark', title: ''},
                    {name: 'aaa', title: '/'},
                    {name: 'bbb', title: ''}
                ]}>
                <PagingState
                    defaultCurrentPage={0}
                    pageSize={12}
                />
                <IntegratedPaging/>
                <ViewUserRole
                    for={['aaa']}
                />
                <CurrencyTypeProvider
                    for={['bbb']}
                />
                <Table messages={{noData: ""}}/>
                <TableHeaderRow/>
                <PagingPanel messages={{
                    info: function info(_ref) {
                        var from = _ref.from,
                            to = _ref.to,
                            count = _ref.count;
                        return '' + from + (from < to ? '-' + to : '') + ' ' + count + '';
                    }
                }}/>
            </Grid>
        );
        if (page === 1) {
            return (
                <div id="role" className="clearfix">
                    <header>
                        {AuthorityComponent.default(() => {
                            return (
                                <Button onClick={() => this.NewRole()}
                                        className={classes.primary}>
                                    <i className="fa fa-plus" aria-hidden="true"></i>
                                    <span style={{marginLeft: 3}}></span></Button>
                            )
                        }, 'authority_role_add')}
                        <Input onChange={(e) => this.onChangeSearch(e)}
                               onKeyDown={(e) => this.onEnter(e)} placeholder="" iconr="fa fa-search"
                               style={{width: 200}}/>
                    </header>
                    <div style={{minHeight: this.state.minHeight}} className="TableComponent">
                        <Spin loading={role.fetching}>
                            <TableComponent rows={role.RoleList}/>
                        </Spin>

                        <Modal
                            aria-labelledby="simple-modal-title"
                            aria-describedby="simple-modal-description"
                            open={this.state.open}
                            onClose={this.handleClose}
                        >
                            <div id="role-modal-detail" className={classes.paper}>
                                <Spin tip="" loading={role.loading}>
                                    <div className="title"><i onClick={() => {
                                        this.handleClose()
                                    }}
                                                              className="closeModal fa fa-close"></i>/
                                    </div>
                                    <div className="grid content">
                                        <div className="col-3-12">
                                            <h3 className="title"></h3>
                                            <div style={{position: 'relative'}}>
                                                <div id="userListScroll" className="userList">
                                                    <ul>
                                                        {role.RoleDetail.users.map((item, i) => {
                                                            return (
                                                                <li key={i}>{item.userName}</li>
                                                            )
                                                        })}
                                                    </ul>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="col-9-12">
                                            <h3 className="title"></h3>
                                            <div style={{position: 'relative'}}>
                                                <div id="viewRoleTree" className="roleTree">
                                                    {role.RoleDetail.roles.arr.map((item, i) => {
                                                        return (
                                                            <Tree setting={{
                                                                check: {
                                                                    enable: false
                                                                }
                                                            }} key={i} id="RoleTree"
                                                                  zNodes={item}>
                                                            </Tree>
                                                        )
                                                    })}
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </Spin>
                            </div>
                        </Modal>
                    </div>
                </
                    div>
            )
        } else {
            return (
                <Spin tip="" loading={role.saveLoading} wrapperStyle="static">

                    <div id="role" className="clearfix">
                        <header>
                            <Button onClick={() => this.setState({page: 1})} className="cancel"> </Button>
                            <Button disabled={role.saveLoading} onClick={() => {
                                this.refs.form1.submit()
                            }}
                                    className={classes.primary}> </Button>
                            <h3>{this.state.editRole ? '' : ''}</h3>
                        </header>
                        <div className="left">
                            <div className="title"></div>
                            <div className="form-role">
                                <Form form='roleForm' initialValues={this.state.editRole || {}}
                                      Submit={(values) => this.Submit(values)} ref="form1"
                                      fields={[{
                                          name: "roleName",
                                          label: "",
                                          required: true
                                      }, {
                                          name: "remark",
                                          label: "",
                                          type: 'textarea',
                                          rows: '5'
                                      }]}/>
                            </div>
                        </div>
                        <div className="content">
                            <Spin loading={this.props.role.fetching} tip="loading..."
                                  style={{minHeight: this.state.minHeight - 120}}>
                                <div className="title"></div>
                                <div className={classes.root}>
                                    <Tabs
                                        value={this.state.value}
                                        onChange={(event, value) => this.handleChange(event, value)}
                                        indicatorColor="primary"
                                        textColor="primary"
                                    >
                                        {role.MenuTree.keys.map((item, i) => {
                                            return (
                                                <Tab key={i} value={i} label={item}/>

                                            )
                                        })}
                                    </Tabs>
                                    <div style={{position: 'relative'}}>
                                        {role.MenuTree.arr.map((item, i) => {
                                            if (value === i) {
                                                return (
                                                    <div key={i} id="treebox"
                                                         style={{height: this.state.minHeight - 120, overflow: 'auto'}}>
                                                        <Tree id="systemRoleTree"
                                                              zNodes={item}></Tree>
                                                    </div>
                                                )
                                            } else {
                                                return null
                                            }
                                        })}
                                    </div>
                                </div>
                            </Spin>
                        </div>
                    </div>
                </Spin>

            )
        }
    }
}

function mapStateToProps(state) {
    return {
        role: state.role
    };
}

const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        actions: bindActionCreators(actions, dispatch),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(App)



