import * as ACTIONS from '../actions'
import {formateTreeData, updateMenuChecked, checkedRoles} from '@/util'

const ACTION_HANDLERS = {
    //loading
    [ACTIONS.ROLE_LOADING_STATE]: (state, action) => (
        Object.assign({}, state, {
            fetching: action.result
        })
    ),
    //
    [ACTIONS.ROLE_TABLE_LIST_FETCH]: (state, action) => (
        Object.assign({}, state, {
            fetching: action.result
        })
    ),
    //
    [ACTIONS.ROLE_TABLE_LIST]: (state, action) => {
        let arr = action.result.map(function (item, i) {
            item['__id__'] = ++i
            return item
        })
        return Object.assign({}, state, {
            RoleList: arr,
            RoleCache: arr,
            fetching: false
        })
    },
    //
    [ACTIONS.ROLE_TABLE_LIST_SEARCH]: (state, action) => (
        Object.assign({}, state, {
            RoleList: action.result
        })
    ),
    //
    [ACTIONS.ROLE_DETAIL_VIEW]: (state, action) => {
        if (!action.id) {
            return Object.assign({}, state, {
                RoleDetail: {
                    roles: formateTreeData(action.roles),
                    users: action.users
                },
                loading: action.loading
            })
        }
        return state
    },
    //
    [ACTIONS.MENU_TREE_LIST]: (state, action) => (
        Object.assign({}, state, {
            rootTree: action.result,
            MenuTree: formateTreeData(action.result.menuBeans),
            fetching: false
        })
    ),
    //
    [ACTIONS.ROLE_EDIT_TREE]: (state, action) => (
        Object.assign({}, state, {
            rootTree: action.rootTree,
            MenuTree: checkedRoles(action.checked, action.all),
            fetching: false
        })
    ),
    //state
    [ACTIONS.MENU_TREE_UPDATE]: (state, action) => (
        Object.assign({}, state, {
            MenuTree: updateMenuChecked(state.MenuTree, action.index, action.result)
        })
    ),
    //  loading
    [ACTIONS.MENU_TREE_SAVE_LOADING]: (state, action) => (
        Object.assign({}, state, {
            saveLoading: action.result
        })
    )
}

const initialState = {
    fetching: false,
    RoleList: [],
    RoleCache: [],
    loading: true,
    RoleDetail: {roles: {arr: []}, users: []},
    MenuTree: {keys: [], arr: []},
    saveLoading: false
}

export function Reducer(state = initialState, action) {
    const handler = ACTION_HANDLERS[action.type]
    return handler ? handler(state, action) : state
}

export default Reducer


