import React, {Component} from 'react'
import './style.less'
import classnames from 'classnames'
export default class Input extends Component {

    static defaultProps = {
        prefixcls: 'input',
        type: 'text',
        disabled: false
    }

    render() {
        const props = this.props
        let className = classnames(props.prefixcls, props.className, props.clazz, {'icon-l': props.iconl}, {'icon-r': props.iconr})
        if (props.type === 'textarea') {
            return <textarea {...props}
                className={className}></textarea>
        }
        if (props.iconl || props.iconr) {
            return (
                <span className="input-icon-wrapper"
                      style={{width:props.style.width,height:props.style.height}}>
                    <input type="text" {...props} className={className}/>
                    {props.iconl && <span className="input-suffix-L"><i className={props.iconl}></i></span>}
                    {props.iconr && <span className="input-suffix-R"><i className={props.iconr}></i></span>}
            </span>
            )
        }
        if (props.readOnly || props.readOnly === 'readOnly' || props.readOnly === 'true') {
            return <input type="text" {...props} className={`${className} readonly`}/>
        }
        return <input type="text" {...props} className={className}/>
    }
}



